* {
    box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to right, rgb(150, 2, 14) 0%, rgb(58, 0, 1) 100%);
  color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

nav {
    display: flex;
    justify-content: flex-end;
    font-size: 1.5em;
}

nav p {
    margin-left: auto;
    border-left: 1px solid lightgray;
    border-bottom: 1px solid lightgray;
    margin: 0px;
    padding: 10px;
    transition: background 0.5s;
    cursor: pointer;
}

nav p:hover {
    background-color: lightgray;
}
.Tilt {
    background: linear-gradient(to right, red, gray);
    color: red;
    font-size: 6em;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    position: absolute;
    top: 10px;
    left: 10px;
}

.Tilt img {
    position: relative;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

.Tilt-inner {
    display: flex;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 600px){
    .Tilt {
        position: relative;
    }
}


.modeBtn:first-of-type {
    border-right: none;
    border-top-left-radius: 10px;
}

.modeBtn:last-of-type {
    border-top-right-radius: 10px;
}

.modeBtn {
    position: relative;
    top: 12px;
    width: 200px;
    font-size: 1.5em;
    padding: 5px;
    background-color: rgb(200, 0, 0);
    -webkit-appearance: none;
    cursor: pointer;
    color: white;
    border: 1px solid white;
    z-index: 1;
}

.modeBtn:focus {
    outline: none;
}

.modeBtn.selected {
    background-color: rgb(150, 0, 0);
    border-bottom: none;
    z-index: 3;
}

.linkForm input {
    width: 100%;
    font-size: 1.5em;
    padding: 10px;
    display: block;
    margin: auto;
}

.linkForm button {
    width: 30%;
    min-width: 100px;
    font-size: 1.5em;
    padding: 10px;
    background-color: rgb(200, 0, 0);
    -webkit-appearance: none;
    cursor: pointer;
    color: white;
    margin: 10px;
    border: 1px solid white;
}

.linkForm {
    display: flex;
}

.linkContainer {
    border: 1px solid white;
    box-shadow: 1px 1px 5px 1px gray;
    max-width: 1000px;
    margin: 10px auto;
    position: relative;
    padding: 20px;
    border-radius: 10px;
    background-color: rgb(150,0,0);
    z-index: 2;
}

.linkContainer p {
    margin: 0px;
}

.imageLinkForm {
    padding: 10px;
}

@media screen and (max-width: 450px){
    .linkContainer {
        border-radius: 0px 0px 10px 10px;
    }
    
    .modeBtn {
        width: 50%;
        font-size: 1.2em;
    }
}
.rank span {
    font-size: 2em;
}

.rank {
    margin-top: 50px;
}
.imageDetection {
    display: flex;
    justify-content: center;
    padding: 0px 10px;
    position: relative;
    max-height: 550px;
}

.imageContainer {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: black;
    border: 1px solid white;
    border-radius: 10px;
}

.imageDetection img {
    max-width: 780px;
    border-radius: 10px;
    max-height: 548px;
}

.imageStats {
    border: 1px solid white;
    width: 200px;
    margin-left: 20px;
    margin-top: 0px;
    border-radius: 10px;
    flex-shrink: 0;
    padding: 5px;
    overflow: scroll;
}

.statHeader {
    color: lightgray;
    -webkit-font-feature-settings: "c2sc";
            font-feature-settings: "c2sc";
    font-variant: small-caps;
}

.statData {
    border-bottom: 1px solid rgba(150,150,150,0.5);
    padding: 10px;
}

.statGroup:last-child .statData {
    border-bottom: none;
}

.imageStats > div {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.statFoodGroup {
    display: flex;
    justify-content: space-between;
    margin: 2px;
    border-bottom: 1px solid rgba(150,150,150,0.5);
}

.statFoodGroup:last-child {
    border-bottom: none;
}

.statFoodName {
    text-transform: capitalize;
    text-align: left;
}
.imageAreaBox {
    border: 3px solid white;
    position: absolute;
}

.imageAreaBox.selected {
    border: 3px solid rgb(0, 255, 3);
}
